* {
  font-family: "Poppins";
  text-transform: lowercase;
}

body {
  margin: 0;
  background-image: url("https://source.unsplash.com/featured/?white,wallpaper");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: rgb(236, 238, 240);
  background-position: center;
  background-attachment: fixed;
}

::-webkit-scrollbar {
  display: none;
}

html,
body {
  min-width: 786px;
}

.App {
  padding-left: 50px;
  height: 100vh;
  backdrop-filter: blur(10px);
}