.hero {
  height: 260px;
  display: flex;
  flex-direction: column;
  align-self: center;
  justify-content: center;
}

div.hero > h1 {
  margin: 0 !important;
  display: flex;
}

div.hero > small {
  font-weight: bold;
}

div.Typewriter {
  cursor: pointer;
  margin-left: 12px;
}

div.icons {
  flex-wrap: nowrap;
  display: flex;
  justify-content: flex-start;
  margin-bottom: 10px;
}

div.college {
  display: flex;
  justify-content: center;
  font-weight: bold;
  align-items: center;
  cursor: pointer;
  width: 160px;
  color: rgb(3, 102, 214);
  font-size: 9px;
  border: 1px solid rgb(225, 228, 232);
  border-radius: 20px;
  background: white;
  padding: 6px;
  line-height: 1.5;
}

div.college > img {
  margin-right: 5px;
}

div.icons > svg {
  width: 25px;
  padding-right: 10px;
}
